.App {
  text-align: center;
  display: flex;
  align-items: center;
  justify-content: center;
  align-content: center;
  height: 100vh;
}

button {
  width: 150px;
  height: 150px;
  border-radius: 50%; 
  border: none;
  color: white;
  font-weight: 900;
  font-size: 2.5rem;
  background: red;
  text-shadow: 0 3px 1px rgba(122,17,8,.8);
  box-shadow: 0 8px 0 rgb(183,9,0), 
    0 15px 20px rgba(0,0,0,.35);
  text-transform: uppercase;
  outline: none; 
  cursor: pointer;
  text-align: center;
}